html {
  font-size: 62.5%; }

.text-cap {
  text-transform: uppercase; }

body {
  position: relative;
  @include resetmargin();
  padding-left: 84px;
  padding-top: 84px;
  background-color: $maincolor;
  font-family: 'Open Sans';
  font-size: 1rem; }

.main-container {
  height: calc(100vh - 84px); }

.main-content-container {
  width: 1171px;
  margin: 0 auto;
  @include media("<large") {
    padding: 0 20px;
    width: 100%; } }

.main-section {
  position: relative; }

.btn {
  padding: 15px 20px;
  background-size: 200% 100%;
  background-image: linear-gradient(to right, $maincolor 50%, $black 50%);
  background-position: 100%;
  border: 2px solid $maincolor;
  color: $maincolor;
  font-family: 'Open Sans';
  font-size: 1.2rem;
  line-height: 1.1rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-position $transition, color $transition;
  &:hover {
    background-position: 0 0;
    color: $black; }
  &:active {
    outline: none; }
  &:focus {
    outline: 1px solid $maincolor; } }

.custom-underline {
  position: relative;
  &:after {
    position: absolute;
    display: block;
    content: "";
    height: 2px;
    bottom: -4px;
    left: 0;
    right: 0;
    background-color: $white; } }

.main-section {
  height: 900px;
  &:nth-of-type(even) {
    background-color: $section-bg; }
  &:nth-of-type(odd) {
    background-color: $white; }
  &--last-card {
    padding-bottom: 50px; } }

.content-title {
  @include resetmargin;
  position: relative;
  padding-left: 68px;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: $black;
  letter-spacing: 1px;
  user-select: none;
  &:before {
    content: "";
    display: block;
    background-color: $black;
    width: 52px;
    height: 1px;
    position: absolute;
    left: 0;
    top: 8px; } }

.title {
  @include resetmargin;
  font-size: 3.6rem;
  font-family: "Yeseva One", serif;
  font-weight: normal; }

.common-text {
  @include resetmargin;
  font-size: 1.5rem;
  line-height: 28px;
  color: $dark-text;
  a {
    color: $dark-text; } }

.white-text {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  color: $white;
  letter-spacing: 1.5px;
  text-decoration: none;
  text-transform: uppercase;
  a {
    color: $white;
    text-decoration: none; } }

.content-card {}


